.button {
	border: none;
	border-radius: 50%;
	width: 2.5em;
	height: 2.5em;
	border: none;
	background: none;
	outline: none;
	padding: 0;
	margin-right: 0.6em;
}

.button:last-child {
	margin-right: 0;
}
.img {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	cursor: pointer;
	transition: opacity 0.5s ease, box-shadow 0.3s ease;
}

.img:hover {
	opacity: 1;
	box-shadow: 0 0 5px rgba(255, 0, 0, 0.5),
		0 0 10px rgba(255, 165, 0, 0.5),
		0 0 15px rgba(255, 255, 0, 0.5),
		0 0 20px rgba(0, 255, 0, 0.5),
		0 0 25px rgba(0, 0, 255, 0.5),
		0 0 30px rgba(75, 0, 130, 0.5),
		0 0 35px rgba(238, 130, 238, 0.5);
}